<template>
  <div>
    <div class="he">
      <!-- 头像展示 -->
      <div class="logoBox" @click="listshow">
        <img src="./imgs/logo.jpg" />
      </div>
      <!-- 下拉框展示 -->
      <transition
        name="fade"
        enter-active-class="fadeInDown"
        leave-active-class="fadeOutUp"
      >
        <div
          class="details"
          v-show="detalistshow"
          style="animation-duration: 0.5s; animation-timing-function= ease-in-out;"
        >
          <ul>
            <li
              v-for="item in itemtext"
              :key="item.id"
              v-show="item.show"
              @click="propertyx(item.property)"
            >
              <a>{{ item.text }}</a>
            </li>
          </ul>
        </div>
      </transition>
      <!-- 菜单按钮 -->
      <button class="fabu" @click="menushow">菜单</button>
    </div>
    <!-- 侧栏 -->
    <div class="celan">
      <transition
        name="fade"
        enter-active-class="fadeInRight"
        leave-active-class="fadeOutRight"
      >
        <div
          class="mb"
          v-show="menuListshow"
          style="animation-duration: 0.5s ; animation-timing-function= ease-in-out;"
        >
          <ul>
            <li v-for="item in menuitem" :key="item.id">
              <a @click="menuProperty(item.property)">{{ item.text }}</a>
            </li>
          </ul>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 选项文字
      itemtext: [
        { id: 0, text: "登录", property: "login", show: true },
        { id: 1, text: "回到主页", property: "gohome", show: true },
        {
          id: 2,
          text: "再次编辑",
          property: "rew",
          show: false,
          // this.$route.path == "/view" ? true : false
        },
        { id: 3, text: "选项4", property: "it3", show: true },
      ],
      // 控制列表显示隐藏
      detalistshow: false,
      // 菜单栏显示隐藏
      menuListshow: false,
      // 菜单选项
      menuitem: [
        { id: 0, text: "切换视图", property: "switchView" },
        { id: 1, text: "编辑", property: "useredit" },
        { id: 2, text: "测试", property: "ls3" },
        { id: 3, text: "菜单选项4", property: "ls4" },
      ],
    };
  },
  methods: {
    listshow() {
      this.menuListshow = false;
      this.detalistshow = !this.detalistshow;
    },
    menushow() {
      this.detalistshow = false;
      this.menuListshow = !this.menuListshow;
    },
    // 头像总事件
    propertyx(p) {
      this.menuListshow = false;
      this.detalistshow = false;
      this[p]();
    },
    login() {
      if (this.$route.name != "login") {
        this.$router.push({
          name: "login",
        });
      }
    },
    gohome() {
      if (this.$route.name != "home") {
        this.$router.push({
          name: "home",
        });
      }
    },
    rew() {
      this.$bus.$emit('rewrite',true)
    },
    it3() {
      console.log(3);
    },

    // 菜单总事件
    menuProperty(p) {
      this[p]();
    },
    switchView() {
      if (this.$route.name != "/release") {
        this.menuListshow = this.detalistshow = false;
        this.$router.push({
          name: "viewTwo",
        });
      }
    },
    useredit() {
      if (this.$route.name != "edit") {
        this.menuListshow = this.detalistshow = false;
        this.$router.push({
          name: "edit",
        });
      }
    },
    ls3() {
      this.menuListshow = this.detalistshow = false;
      console.log("测试");
      this.$store.dispatch("senddata");
    },
    ls4() {
      this.menuListshow = this.detalistshow = false;
      console.log(4);
    },
  },
  mounted() {
    this.$bus.$on("sendData", (data) => {
      this.detalistshow = data;
      this.menuListshow = data;
    });
    this.$bus.$on("showitem", (data) => {
      setTimeout(() => {
        this.itemtext[2].show = data;
      }, 500);
    });
    this.$bus.$on("default", (data) => {
      setTimeout(() => {
        this.itemtext[2].show = data;
      }, 500);
    });
  },
};
</script>

<style scoped>
.he {
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background-color: #3dc1d3;
}
.logoBox {
  width: 75px;
}
.he img {
  width: 100%;
  border-radius: 20px;
}
/* 个人详细信息 */
.details {
  position: absolute;
  background-color: #63ccdaa9;
  top: 100%;
  box-sizing: border-box;
  padding: 25px 20px;
  min-width: 250px;
  overflow: hidden;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
.details li {
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  padding: 10px 0;
}
.details a {
  text-decoration: none;
  color: black;
}
.fabu {
  border-radius: 10px;
  width: 100px;
  height: 50px;
  position: absolute;
  right: 5%;
  border: 2px solid black;
  background-color: transparent;
}
/* 侧栏 */
.celan {
  position: relative;
}
.celan .mb {
  width: 20vw;
  background-color: #f8a5c296;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 20px;
  border-bottom-left-radius: 14px;
}
.mb li {
  height: 10vh;
  list-style: none;
  font: 700 1rem "";
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.mb li a {
  text-decoration: none;
  color: white;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  user-select: none;
}
.celan li:hover:after {
  content: " ";
  position: absolute;
  bottom: 0;
  height: 1px;
  background-color: #fff;
  animation: line 0.5s both ease-in;
}
/* 切换显示动画 */
.dataListA-enter {
  transform: scale(1);
}
.dataListA-enter-to {
  transform: scale(0);
}
.dataListA-leave {
  transform: scale(0);
}
.dataListA-leave-to {
  transform: scale(1);
}

@keyframes line {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
    border-left: 1px solid white;
  }
}
</style>
