import VueRouter from "vue-router";

// ! 重写push方法
const outPush = VueRouter.prototype.push;
VueRouter.prototype.push = function (locationx, res, err) {
  if (res || err) {
    outPush.call(this, locationx, res, err);
  } else {
    outPush.call(
      this,
      locationx,
      () => {},
      () => {}
    );
  }
};
// ! 重写 replace 方法
const outReplace = VueRouter.prototype.replace;
VueRouter.prototype.replace = function (locationx, res, err) {
  if (res || err) {
    outReplace.call(this, locationx, res, err);
  } else {
    outReplace.call(
      this,
      locationx,
      () => {},
      () => {}
    );
  }
};

const router = new VueRouter({
  routes: [
    {
      path: "*",
      redirect: "home",
    },
    {
      name: "home",
      path: "/home",
      component: () => import("../views/HomeCP.vue"),
      meta: { show: true },
    },
    {
      name: "login",
      path: "/login",
      component: () => import("../views/login.vue"),
      meta: { show: true },
      beforeEnter: async (to, from, next) => {
        next();
      },
    },
    {
      name: "register",
      path: "/register",
      component: () => import("../views/register.vue"),
      meta: { show: true },
    },
    {
      name: "viewTwo",
      path: "/view/two",
      component: () => import("../views/viewTwo.vue"),
      meta: { show: false },
    },
    {
      name: "edit",
      path: "/useredit",
      component: () => import("../views/edit.vue"),
      meta: { show: true },
    },
    {
      name: "viewOne",
      path: "/view",
      component: () => import("../views/viewOne.vue"),
      meta: { show: true },
      children: [
        {
          name: "v",
          path: "v/:id?",
          component: () => import("../views/ViewOneChidren/index.vue"),
          meta: { show: true },
        },
      ],
    },
  ],
});

export default router;
