import { RequestMock } from "../../api/index";
const actions = {
  async getlogin({ commit }) {
    let logindata = await RequestMock();
    if (logindata.code == 200) {
      commit("GETLOGIN", logindata);
    }
  },
};
const mutations = {
  GETLOGIN(satte, data) {
    satte.logindata = data;
  },
};
const state = {
  logindata: [],
};
export default {
  actions,
  mutations,
  state,
};
