import { RequestMock } from "../../api/index";

const actions = {
  async senddata({ commit }) {
    let date = await RequestMock();
    if (date.code == 200) {
      commit("SENDDATA", date.data);
    }
  },
};

const mutations = {
  SENDUSERINPUT(state, data) {
    state.userinput.unshift(data);
    localStorage.setItem("userdata", JSON.stringify(state.userinput));
  },
  SENDDATA(state, data) {
    console.log(1);
    console.log(data);
    state.dataa = data;
  },
};

const state = {
  //? 用户输入
  userinput: JSON.parse(localStorage.getItem("userdata")) || [],
  dataa: [],
};

export default {
  actions,
  mutations,
  state,
};
