import axios from "axios";

const requset = axios.create({
  baseURL: "/mock",
  timeout: 5000,
});
// 请求
requset.interceptors.request.use((config) => {
  return config;
});
// 响应
requset.interceptors.response.use(
  (res) => {
    return res.data;
  },
  (err) => {
    Promise.reject(new Error("faile"));
  }
);
export default requset;
